@font-face {
  font-family: "Gilroy";
  font-weight: 800;
  font-style: normal;

  font-display: swap;
  src: url("../fonts/subset-Gilroy-ExtraBold.woff2")
      format("woff2"),
    url("../fonts/subset-Gilroy-ExtraBold.woff")
      format("woff");
}

@font-face {
  font-family: "Gilroy";
  font-weight: 300;
  font-style: normal;

  font-display: swap;
  src: url("../fonts/subset-Gilroy-Light.woff2")
      format("woff2"),
    url("../fonts/subset-Gilroy-Light.woff") format("woff");
}

body {
  font-family: Gilroy, sans-serif;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
